<template>
  <div class="seller-order-list">
    <back-header title="全部订单">
      <img src="../order-list/search@3x.png" class="search" alt="">
    </back-header>
    <ul class="tabs" style="height: 44px;">
      <li @click="changeTab(0)" :class="{active: type === 0}">待付款 <i></i></li>
      <li @click="changeTab(1)" :class="{active: type === 1}">待发货 <i></i></li>
      <li @click="changeTab(2)" :class="{active: type === 2}">已发货 <i></i></li>
      <li @click="changeTab(3)" :class="{active: type === 3}">已完成 <i></i></li>
      <li @click="changeTab(4)" :class="{active: type === 4}">退款 <i></i></li>
    </ul>
    <scroll class="scroll" :pull-down-refresh="{threshold: 70, stop: 56}" @pullingDown="pullingDown" ref="scroll" @scrollToEnd="loadMore" :is-finished="finished" clickable>
      <div class="order" @click="$router.push('/seller/order/' + item.id + '?type=' + type)" v-for="item in list" :key="item.id">
        <div class="order-no">
          <p>订单编号：{{ item.order_no }}</p>
          <span>{{ item.create_time.slice(5) }}</span>
        </div>
        <order-item class="item" v-for="good in item.goods" :key="good.id" :size-drop="false" :goods="good" price-show />
        <div class="btns" v-if="type===1">
          <button>联系买家</button>
          <button @click.stop="delivery(item)">发货</button>
        </div>
      </div>
    </scroll>
  </div>
</template>

<script>
import BackHeader from '../../components/back-header/back-header'
import OrderItem from '../../components/order-item/order-item'
import Scroll from '../../components/scroll/scroll'
import { post } from '../../common/js/http'

const PAGE_SIZE = 20

export default {
  name: 'seller-order-list',
  components: {
    BackHeader,
    OrderItem,
    Scroll
  },
  data() {
    return {
      type: 0,
      list: [],
      page: 1,
      finished: true
    }
  },
  created() {
    this.type = Number(this.$route.params.type)
  },
  mounted() {
    this.getList()
  },
  activated() {
    if (Number(this.$route.params.type) !== this.type && !this.$router.isBack) {
      this.type = Number(this.$route.params.type)
    }
    if (!this.$router.isBack) {
      this.page = 1
      this.list = []
      this.getList()
    }
  },
  methods: {
    changeTab(tab) {
      if (this.type === tab) return
      this.type = tab
      this.page = 1
      this.list = []
      this.getList()
    },
    pullingDown() {
      this.page = 1
      this.pulling = true
      this.getList()
    },
    loadMore() {
      if (this.loading) return
      this.page++
      this.loading = true
      this.getList()
    },
    getList() {
      let loading = null
      if (this.page === 1 && !this.pulling) {
        loading = this.$skeLoading('.seller-order-list', '88px')
      }
      post('/shop/shop/shopOrderList', {
        type: this.type,
        page: this.page,
        size: PAGE_SIZE
      })
        .then(({ data: { data } }) => {
          const list = data.map(item => {
            if (item.suborder) {
              return {
                ...item.suborder,
                id: item.ordergoodsid,
                goods: [{
                  ...item.goods,
                  size: item.goods.color + (item.size || ''),
                  cover: item.goods.goods.thumb_urls.split(',')[0]
                }]
              }
            }
            item.goods = item.goods.map(goods => {
              return {
                ...goods,
                title: goods.goods.title,
                goods: null,
                size: goods.color + (goods.size || ''),
                cover: goods.goods.thumb_urls.split(',')[0]
              }
            })
            return item
          })
          this.list = this.page === 1 ? list : this.list.concat(list)
          this.finished = data.length < PAGE_SIZE
          this.loading = false
          loading && loading.close()
          this.$nextTick(() => {
            this.$refs.scroll.refresh()
          })
          this.$refs.scroll.closeRefresh()
          this.pulling = false
        })
        .catch(() => {
          loading && loading.close()
        })
    },
    delivery(item) {
      this.$router.push({
        name: 'delivery',
        params: {
          goods: item.goods,
          orderId: item.id
        }
      })
    }
  }
}
</script>

